<template>
  <div>
    <form @submit.prevent="submitUser">
      <input v-model="loginUser.email" placeholder="Your email address" required>
      <input v-model="loginUser.password" placeholder="Password" required type="password">
      <button>Login</button>
    </form>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {

    computed: mapState({
      loginUser: function(state) {
        return state.loginUser
      },
      currentUser: function(state) {
        return state.currentUser
      }
    }),
    methods: {
      submitUser() {
        this.$store.dispatch('userLogin').then(() => {
          debugger
          if(!this.formErrors) {
            this.$router.push({ name: "Home" })
          }
        })
      },
    },
    beforeMount() {
      this.$store.dispatch('loadCSRF')
      this.$store.dispatch('loadCookie')

      '.eJwlj0tqAzEQBe-itRcttfrnyxipPyQEEpixVyF3z4QsH0VBve_2qCPPt3Z_Hq-8tcd7tHsT0c6qPjsNllp97yyBmZzTd3TQi5f0AtIycVAxx7HLjRzJCzepeerK1A6OfzuEIqsYJuwVfeWKmWtUoLEhCbEYYBiKtFvz86jH8-sjP6-e4bTHKi2AMIPByATJFaP3QAIGqw3Ol_c68_g_Ma39_ALj0z7q.XfrDvA.u2SwInof22R2WSWlbjNWvo5WjvU'
      if (this.currentUser === null) {
        this.$store.dispatch('setCurrentUser')
      }
    }
  }
</script>



